.App {
  text-align: center;
  color:white
}

.arr{
  display: flex;
  justify-content: center;
}

.ele{
  margin:0.5em;
  font-size: 10px;
  width: 5px;
  color: black;
}

button{
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 3rem;
  background-color: white;
  border: none;
  padding:10px;
  border-radius: 10px;
  color:black;
  transition: all 450ms;
  font-weight: 500;
  font-size: 15px;
}

button:hover{
  cursor: pointer;
  transform: scale(1.09);
  background-color: rgb(66, 233, 224);
}

p{
  font-size: 2em;
  font-weight: 500;
}

.inp{
  margin-left: 1rem;
  margin-right: 1rem;
  text-align: center;
  width: 50px;
  height: 30px;
}

.btns{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.resp{

}

.warn{
  display: none;
  font-size: small;
}



@media screen and (max-width: 800px) {
  button{
    font-size: 10px;
    padding: 5px;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    margin-bottom: 1rem;
    max-width: 80px;
  }

  .inp{
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    margin-bottom: 5px;
    padding: 0px;
    height: 20px;
  }

  .btns{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .resp{
    display: flex;
  }

  .ele{
    margin: 0.1em;
    width: 5px;
  }

  .warn{
    display: block;
  }
}

@media screen and (max-width: 500px) {
  button{
    font-size: 12px;
    padding: 5px;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    margin-bottom: 1rem;
    max-width: 80px;
  }

  .inp{
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    margin-bottom: 5px;
    padding: 0px;
    height: 20px;
  }

  .btns{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .resp{
    display: flex;
  }

  .ele{
    margin: 0.05em;
    width: 5px;
  }

  .arr{
    transform: rotate(0deg);
  }

  .warn{
    display: block;
  }
}